body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.octave td.note {
    width: 60px;
    height: 100px;
    border: 1px solid black;
    margin: 5px;
}

td.note {
    background-color: grey;
}

.octave td.note.playing {
    /* background-color: rgb(118, 255, 118); */
}

td.note.selected {
    background-color: white;
}
.App {
  text-align: center;
}

.App > table {
  width: 100%;
}

.App.c-std .header.note-c {
  color: green;
}
.App.c-major .header.note-c-sharp {
  color: green;
}
.App.d-std .header.note-d {
  color: green;
}
.App.d-major .header.note-d-sharp {
  color: green;
}
.App.e-std .header.note-e {
  color: green;
}
.App.f-std .header.note-f {
  color: green;
}
.App.f-major .header.note-f-sharp {
  color: green;
}
.App.g-std .header.note-g {
  color: green;
}
.App.g-major .header.note-g-sharp {
  color: green;
}
.App.a-std .header.note-a {
  color: green;
}
.App.a-major .header.note-a-sharp {
  color: green;
}
.App.b-std .header.note-b {
  color: green;
}
table.explaination-table {
    width: 100%;
    margin: 10px;
    border: 1px solid #BBBBBB;
}

.play-key, .skip-key {
    /* 
    display: inline-block;
    width: 100px;
    height: 100px;
    */
}
.play-key {
    background-color: #FFFFFF;
}
.skip-key {
    background-color: #BBBBBB;
}
.start-key {
    font-weight: bold;
}
.major-break {
    padding-top: 200px;
}

